* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #c9c9c9;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  border-radius: 5px;
  margin-bottom: 16px;
  padding: 8px;
}

li {
  margin-bottom: 20px;
  list-style: none;
  animation: animation 1s;
  display: flex;
}

@keyframes animation {
  0% {
    transform: scale(.5);
  }

  100% {
    transform: scale(1);
  }
}

p {
  text-transform: uppercase;
  margin-left: 10px;
  font-weight: bold;
  display: flex;
}

section {
  animation: animation .5s;
}

#search-box:focus {
  box-shadow: 10px 10px 8px 2px rgba(0, 0, 0, .3);
}

